import React from "react";
import { withStyles } from '@material-ui/core';
import Container from '@material-ui/core/Container';
import abasse from "./Images/AbasseLogo.png";
import Grid from '@material-ui/core/Grid';
import Avatar from '@material-ui/core/Avatar';
import './Images/Css/Acceuil.css'
import VizSensor from 'react-visibility-sensor'; // or use any other 3rd party plugin or define your own
import Fade from '@material-ui/core/Fade';



const styles = {
    paper: {
        //backgroundImage: `url(${background})`,
        paddingTop: "1vh",
        paddingLeft: "15vw",
        width: "100vw"
    },
    avatarComputer: {
        width: "15vw",
        height: "15vw",
        marginBottom: "7vh",
        marginLeft: "42vw"

    },
    avatarMobile: {
        width: "20vw",
        height: "20vw",
        marginBottom: "7vh",
        marginLeft: "39vw"

    },
    textComputer: {
        marginTop: "5vw",
    },
    textMobile: {
        margin: 0
    },
    title: {
        background: "#fcd238",
        marginTop: 0
    },
    containerComputer: {
        width:"98.7vw",
        height:"70vh",
        background:"#fcd238",
        flexGrow: 1,
    },
    containerMobile: {
        width:"98.7vw",
        height:"100vh",
        background:"#fcd238",
        marginLeft: 0,
        flexGrow: 1,
    }
};

class AboutMe extends React.Component {

    constructor(){
        super();
        this.state = {isVisible: false,isMobile : window.innerWidth > 1080 ? false : true};
    }

    updateComponentSize = () => {
        let isMobile = this.state.isMobile;
        if (window.innerWidth > 1080 ) {
            isMobile = false
        }
        else{
            isMobile = true;
        }
        this.setState({isMobile});
    }

    componentDidMount() {
        window.addEventListener("resize", this.updateComponentSize);
    }

    componentWillUnmount() {
        window.removeEventListener("resize", this.updateComponentSize);
    }

    handleFade(isVisible) {
        this.setState({isVisible})
    }

    render() {
        const styleAbout = this.state.isMobile ? "" : "box";
        const styleAvatar = this.state.isMobile ? this.props.classes.avatarMobile : this.props.classes.avatarComputer;
        return (
            <VizSensor //librairies permettant d'afficher le composant uniquement si sa position se trouve dans l'Ã©cran visible
                onChange={(isVisible) => {
                    this.handleFade(isVisible);
                }}
            >
                        <div id={styleAbout} className={this.props.classes.containerMobile}>
                            <center><h1 className={this.props.classes.title}>Me concernant</h1></center>
                        
                                <Avatar alt="Abasse FIDALY" src={abasse} className={styleAvatar} />
                                <Container><strong>PassionnÃ© par l'informatique,les nouvelles technologies, je travaille Ã  l'INSEE en tant que dÃ©veloppeur fullstack. 
                                Autodidacte, et polyvalent je dÃ©sire faire  carriÃ¨re dans l'informatique afin d'acquÃ©rir de l'expertise dans le dÃ©veloppement web
                                </strong>
                                </Container>
                        </div>
                                    
            </VizSensor>
            
        )
    }
}

export default withStyles(styles)(AboutMe)

